.product {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 1rem;
  border-radius: 15px;
  width: 250px;
  height: auto;
  box-shadow:  1px 1px 3px rgba(0,0,0,0.12), 0 1px 2px  rgba(0,0,0,0.24);
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
}

.product:hover{
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.details h3 {
  padding:0.5rem;
}


.product img {
    width: 50%;
    height: auto;
}
.product .details {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 0.5rem;
}

.price {
  display: flex;
  flex-flow: row-reverse;
  padding: 0.3rem;
}

.product .price span {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  font-weight: 700;
  padding: 2px;

}

.product-btn{
  width: 100%;
}
.product button {
  padding: 0.5rem;
  width: 100%;
  height: 40px;
  border-radius: 5px;
  font-weight: 400;
  letter-spacing: 1.15px;
  background-color: #000000;
  color: #f9f9f9;
  border: none;
  outline: none;
  cursor: pointer;
} 
.product button:hover {
  opacity: 0.8;
  transition: .5s;
} 

.product-btn :nth-child(2){
  background-color: #FFD700;
  color : black;
  font-weight: bold;
}

@media (max-width:600px){
  .product {

    border-radius: 0;
    margin: 1rem 0;
  }

  .product button{
    border-radius: 0;
  }
  
}